@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
body {
  background-color: #ffffff;
  min-width: 320px;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  height: 100%;
  color: #333333;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 320px;
  box-shadow: 0 0 15px 0 #00000020;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page-container {
    width: 1200px;
  }
}
.main-header {
  position: relative;
  margin: 0;
}
.main-header__toggle {
  position: absolute;
  width: 23px;
  height: 22px;
  top: 17px;
  right: 20px;
  border: none;
  cursor: pointer;
  background-color: #ffffff;
}
.main-header__toggle--close {
  display: none;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 8px 0 0 #81B3D2, 0 16px 0 0 #81B3D2;
}
.main-header__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.6), 0 16px 0 0 rgba(129, 179, 210, 0.6);
}
.main-header__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 16px 0 0 rgba(129, 179, 210, 0.3);
}
.main-nav {
  position: relative;
  min-height: 56px;
  margin: 0;
}
.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.main-nav__toggle--close {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 4px;
  height: 28px;
  background-color: #ffffff;
}
.main-nav__toggle::before:hover,
.main-nav__toggle::after:hover {
  color: #000000;
}
.main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__toggle:hover::before,
.main-nav__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav__toggle:active::before,
.main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    justify-content: center;
    position: relative;
    grid-template-columns: repeat(5, 153px);
    background-color: #ffffff;
    z-index: 1;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    grid-template-columns: repeat(5, 240px);
  }
}
