  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  position: relative;
}
@media (min-width: 768px) {
  .impression__title {
    font-size: 21px;
    margin: 0 0 31px 0;
    padding: 0 15px 0 0;
  }
  .impression__title--line::before {
    content: "";
    position: absolute;
    width: 123px;
    height: 1px;
    background: #dbdbdb;
    right: -21px;
    top: 53%;
  }
  .impression__title--line::after {
    content: "";
    position: absolute;
    width: 123px;
    height: 1px;
    background: #dbdbdb;
    left: -37px;
    top: 53%;
  }
}
@media (min-width: 1200px) {
  .impression__title {
    font-size: 21px;
    margin: 0 0 43px 0;
    padding: 0 32px 0 0;
  }
  .impression__title--line::before {
    width: 280px;
    right: -36px;
  }
  .impression__title--line::after {
    left: -55px;
    width: 280px;
  }
}
.impression__radio-label {
  position: relative;
  display: block;
  padding: 10px 0 26px 63px;
}
@media (min-width: 768px) {
  .impression__radio-label {
    padding: 10px 0 26px 63px;
    width: 295px;
  }
}
@media (min-width: 1200px) {
  .impression__radio-label {
    padding: 16px 18px 26px 63px;
    width: 270px;
  }
}
.impression__radio-label:hover .impression__radio-mark {
  background-color: #E5E5E5;
}
.impression__radio-label:active .impression__radio-mark {
  background-color: #d5d5d5;
}
@media (min-width: 1200px) {
  .impression__radio-label:nth-child(3) {
    margin: 0;
  }
}
.impression__radio-mark {
  border-radius: 50%;
  display: inline-block;
  background: #F2F2F2;
  position: absolute;
  top: 0;
  left: 0;
  width: 46px;
  height: 46px;
  cursor: pointer;
}
.impression__radio-mark::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  display: none;
  background-color: #333333;
  border-radius: 50%;
}
.impression__radio-label input:checked + .impression__radio-mark::before {
  display: block;
}
.impression__radio-label input[type="radio"]:focus + .impression__radio-mark {
  outline: auto;
}
