.visited__checkbox-mark {
  background-color: #F2F2F2;
  width: 46px;
  position: absolute;
  top: 0;
  left: 0;
  height: 46px;
  cursor: pointer;
  border-radius: 4px;
  margin-bottom: 3px;
}
.visited__checkbox-mark::before {
  content: "";
  background-image: url("../img/inner/tick.svg.svg");
  background-repeat: no-repeat;
  top: 15px;
  left: 12px;
  position: absolute;
  display: none;
  width: 22px;
  height: 16px;
}
.visited__check-label input:checked + .visited__checkbox-mark::before {
  display: block;
}
.visited__check-label input[type="checkbox"]:focus + .visited__checkbox-mark {
  outline: auto;
}
